import { Col, Form, Row } from "antd";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { LoginModel, LoginResponseModel } from "../../models/authModels";
import { AuthService } from "../../services/authService";
import { MyFormItemInput, MyFormItemInputPassword } from "../core/Input";
import { MyButton } from "../core/MyButton";
import MyTitle from "../core/Title";

export default function LoginComponent() {
    let navigate = useNavigate();
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    function handleLogin() {
        AuthService.CheckLogin(new LoginModel(userName, password), (data: any) => {
            new LoginResponseModel(data);
            navigate('/');
        });
    }

    return (
        <div className="my-box">
            <Row gutter={12}>
                <Col span={23}>
                    <MyTitle value="Đăng nhập" />
                    <Form layout="vertical">
                        <MyFormItemInput
                            label="Tên đăng nhập"
                            value={userName}
                            setValue={setUserName} />

                        <MyFormItemInputPassword
                            label="Mật khẩu"
                            value={password}
                            setValue={setPassword} />

                        <MyButton
                            onClick={handleLogin}
                            value="Đăng nhập"
                            type="primary" />
                    </Form>
                </Col>
            </Row>

        </div>
    );
}